/* Background Video */
.bg-ytvideo{
	    position: absolute !important;
    height: 100%;
    width: 100%;
    top: 0;
    z-index: 0 !important;
}
.bg-video {
	//position: absolute;
    width: 100%;
    overflow: hidden;
    behavior: url(/PIE.htc);
    transform: translateZ(0);
    display: block;
    border-radius: 0;
}

.bg-video iframe {
    display: block;
    position: absolute;
	z-index: -2;
}
.bg-video .bg-player {
	bottom: 0;
	left: 0;
	right: 0;
	position: absolute;
	top: 0;
	width: 100%;
	background: url(polina.jpg) no-repeat;
	min-height: 500px;
}

.bg-video .vidbg {
	min-width: 100%;
	min-height: 100%;
}

