/* Postions */
.fixed {
  position: fixed;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.static {
  position: static;
}
/* Zindex*/
.zindex-1 {
  z-index: 1;
}

.zindex-2 {
  z-index: 2;
}

.zindex-3 {
  z-index: 3;
}
/* Borders */
.border-all {
  border: 1px solid @color-theme;
}

.border-top {
  border-top: 1px solid @color-theme;
}

.border-bottom {
  border-bottom: 1px solid @color-theme;
}

.border-right {
  border-right: 1px solid @color-theme;
}

.border-left {
