}
@media only screen and (max-width : 767px ) {
  section {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  .text-center-xs {
    text-align: center !important;
  }

  .pull-none-xs {
    float: none !important;
    text-align: center !important;
  }

  .mb-15-xs {
    margin-bottom: 15px;
  }

  .mb-30-xs {
    margin-bottom: 30px !important;
  }

  .mb-50-xs {
    margin-bottom: 50px;
  }

  .mb-60-xs {
    margin-bottom: 60px !important;
  }

  .p-none-xs {
    padding-right: 0;
    padding-left: 0;
  }
}
@media only screen and (min-width : 768px) and (max-width : 991px) {
  .text-center-sm {
    text-align: center !important;
  }

  .mb-0-sm {
    margin-bottom: 0;
  }

  .mb-15-sm {
    margin-bottom: 15px;
  }

  .mb-30-sm {
