  }

  .pb-50-sm {
    padding-bottom: 50px;
  }

  .pb-60-sm {
    padding-bottom: 60px;
  }

  .p-none-sm {
    padding-right: 0;
    padding-left: 0;
  }

  .pull-none-sm {
    float: none !important;
    text-align: center !important;
  }
}

